







































var userLoginInfo;
export default {
	data() {
		return {
			bankName: '',
			cardTypeName: '',
			PersonName: '',
			Info: '',
			mobile: '',
			// id 存在是保存操作  不存在为新增操作
			id: null
		}
	},
	onLoad(params) {
		userLoginInfo = this.userLoginInfo('../addAdress/adress', 1);
		// bankCode:"CIB",
		// bankName:"兴业银行",
		// cardType:"DC",
		// cardTypeName:"储蓄卡"
		this.bankName = JSON.parse(params.addInfo).bankName;
		this.cardTypeName = JSON.parse(params.addInfo).cardTypeName;
		this.PersonName = JSON.parse(params.addInfo).PersonName;
		this.Info = JSON.parse(params.addInfo).Info;
	},
	methods: {
		// 添加银行卡
		addCard() {
			uni.request({
				url: this.apiUrl + 'user/saveBank',
				method: 'POST',
				header: {
					'content-type': "application/x-www-form-urlencoded"
				},
				data: {
					uid: userLoginInfo.id,
					bank: this.bankName,
					card_num: this.Info,
					card_type: this.cardTypeName,
					card_name: this.PersonName,
					card_mobile: this.mobile
					// id:this.id
				},
				success: (res) => {
					if (res.data.code == 0) {
						uni.showToast({
							title: '添加成功',
							icon: 'none',
							duration: 1000,
							success: (res) => {
								uni.redirectTo({
									url: './addCard'
								});
							}
						});
					}else {
						uni.showToast({
							title: '该银行已经被添加',
							icon: 'none',
							duration: 1000,
						});
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		},
	}
}
